<template>
  <section v-loading="listLoading" class="page-container">
    <!--工具条-->
    <div class="toolbar">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.id" clearable placeholder="编号" />
        </el-form-item>
        <el-form-item>
          <el-input v-model="filters.userName" clearable placeholder="用户名" />
        </el-form-item>
        <el-form-item>
          <el-select v-model="filters.entityType" clearable placeholder="评论对象">
            <el-option label="话题" value="topic" />
            <el-option label="文章" value="article" />
            <el-option label="评论" value="comment" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input v-model="filters.entityId" clearable placeholder="对象编号" />
        </el-form-item>
        <el-form-item>
          <el-select v-model="filters.status" clearable placeholder="请选择状态" @change="list">
            <el-option label="正常" value="0" />
            <el-option label="删除" value="1" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="list"> 查询 </el-button>
        </el-form-item>
      </el-form>
    </div>

    <div v-if="results && results.length > 0" class="page-section comments-div">
      <ul class="comments">
        <li v-for="item in results" :key="item.id">
          <div class="comment-item">
            <avatar :user="item.user" />
            <div class="content">
              <div class="meta">
                <span class="nickname">
                  <a :href="('/user/' + item.user.id) | siteUrl" target="_blank">{{
                    item.user.nickname
                  }}</a>
                </span>

                <span>ID: {{ item.id }}</span>

                <span class="create-time">@{{ item.createTime | formatDate }}</span>
                <span v-if="item.entityType === 'article'">
                  <a :href="('/article/' + item.entityId) | siteUrl" target="_blank">
                    <el-tag type="info" size="mini">文章：{{ item.entityId }}</el-tag>
                  </a>
                </span>

                <span v-if="item.entityType === 'topic'">
                  <a :href="('/topic/' + item.entityId) | siteUrl" target="_blank">
                    <el-tag type="info" size="mini">话题：{{ item.entityId }}</el-tag>
                  </a>
                </span>

                <span v-if="item.entityType === 'comment'">
                  <el-tag type="info" size="mini">评论：{{ item.quoteId }}</el-tag>
                </span>

                <div class="tools">
                  <span v-if="item.status === 1" class="item info">已删除</span>
                  <a v-else class="item" @click="handleDelete(item)">删除</a>
                </div>
              </div>
              <div class="summary" v-html="item.content" />
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div v-else class="page-section comments-div">
      <div class="notification is-primary">
        <strong>无数据 或 输入相应参数进行查询</strong>
      </div>
    </div>

    <div v-if="page.total > 0" class="pagebar">
      <el-pagination
        :page-sizes="[20, 50, 100, 300]"
        :current-page="page.page"
        :page-size="page.limit"
        :total="page.total"
        layout="total, sizes, prev, pager, next, jumper"
        @current-change="handlePageChange"
        @size-change="handleLimitChange"
      />
    </div>
  </section>
</template>

<script>
import Avatar from "@/components/Avatar";
export default {
  components: { Avatar },
  data() {
    return {
      results: [],
      listLoading: false,
      page: {},
      filters: {},
      selectedRows: [],
    };
  },
  mounted() {
    // this.list()
  },
  methods: {
    async list() {
      var me = this;
      this.listLoading = true;
      const params = Object.assign(this.filters, {
        skipCount: this.getSkipCount(me.page.page, me.page.limit),
        maxResultCount: me.page.limit,
      });
      try {
        var data = await this.axios.post("/api/app/comment/paged", params);
        this.results = data.items;
        this.page.total = data.totalCount;
      } finally {
        this.listLoading = false;
      }
    },
    handlePageChange(val) {
      this.page.page = val;
      this.list();
    },
    handleLimitChange(val) {
      this.page.limit = val;
      this.list();
    },
    handleSelectionChange(val) {
      this.selectedRows = val;
    },
    handleDelete(row) {
      const me = this;

      this.$confirm("确认要删除评论？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.axios
            .delete(`/api/app/comment/${row.id}`)
            .then((data) => {
              me.$message.success("删除成功");
              me.list();
            })
            .catch((rsp) => {
              me.$notify.error({ title: "错误", message: rsp.message });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
};
</script>

